.vertical-header {
    position: fixed;
    left: 0;
    z-index: 1111;
    top: 0;
    bottom: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0)
}

.vertical-header-wrapper {
    width: 100%;
    height: 100%;
    /* background: rgba(0, 0, 0, 1) url("images/menubg.jpg") no-repeat scroll center center; */
    z-index: 10;
    overflow-x: hidden;
    min-width: 260px;
    overflow-y: hidden;
    opacity: 0.8;
    background: #000;
}

.subsub-menu-wrapper,
.sub-menu-wrapper {
    opacity: 0;
    visibility: hidden;
    background-color: #151515;
    position: absolute;
    padding: 0px 10px 0;
    width: 100%;
    top: 0;
    max-width: 240px;
    bottom: 0;
    left: 60%;
    height: 100%;
    display: table;
    -webkit-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
    transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
}

.subsub-menu-wrapper {
    background-color: #191919;
}

.primary-menu li:hover .subsub-menu li:hover a,
.primary-menu li:hover .sub-menu-wrapper li:hover a,
.primary-menu li:hover a {
    color: rgba(255, 255, 255, 1);
}

.primary-menu li:hover .subsub-menu li a,
.primary-menu li:hover .sub-menu-wrapper li a,
.menu-search .btn,
.vertical-header li a {
    color: rgba(255, 255, 255, 0.7);
}

.primary-menu li:hover .subsub-menu li a {
    color: rgba(255, 255, 255, 0.7) !important;
}

.primary-menu li:hover .subsub-menu li:hover a {
    color: rgba(255, 255, 255, 1) !important;
}

.vertical-header li {
    list-style: none;
}

.vertical-header ul {
    padding: 0 15px;
}

.sub-menu li:hover .subsub-menu-wrapper,
.child-menu:hover .sub-menu-wrapper {
    opacity: 1;
    z-index: 1000;
    left: 100%;
    position: absolute;
    visibility: visible;
}

.primary-menu li a {
    display: block;
    width: 100%;
    position: relative;
    text-decoration: none !important;
}

.primary-menu li a:hover {
    color: #c2d82e;
    font-weight: bold;
}

.primary-menu li .fa-angle-right {
    position: absolute;
    right: 0;
    font-size: 14px;
    top: 0px;
}

.yamm .fa-shopping-bag,
.navbar-inverse .fa-shopping-bag,
.primary-menu .fa-shopping-bag {
    margin-left: 4px;
}

.primary-menu {
    list-style: none;
}

@media(max-width:768px) {
    .primary-menu {
        list-style: none;
        margin-top: -15px;
    }
}

.primary-menu li {
    text-transform: capitalize;
    padding: 15px 30px;
    /* border-bottom: 1px dotted rgba(255, 255, 255, 0.15); */
    font-size: 14px;
    line-height: 1 !important;
    margin: 0 !important;
    text-transform: capitalize;
    white-space: nowrap;
    font-family: 'Montserrat', sans-serif;
}

.primary-menu li:first-child {
    border-top: 0;
}

.primary-menu li:last-child {
    border-bottom: 0;
}

.center-content {
    display: table-cell;
    vertical-align: middle;
}

.margin-block {
    min-height: 40px;
}

.logo {
    padding: 30px 0 0;
    text-align: center;
}

.logo img {
    width: 150px;
    height: 150px;
    border-radius: 100%;
}

.menu-search {
    padding: 0 15px;
}

.menu-search .form-group {
