@media(max-width:768px) {
    .navbar-default .navbar-nav>.open>a,
    .navbar-default .navbar-nav>.open>a:focus,
    .navbar-default .navbar-nav>.open>a:hover {
        background-color: #121212;
        color: #ffffff;
    }
    .navbar-collapse {
        border: 0;
        box-shadow: none;
    }
    .newsletter-widget .btn {
        display: block;
        width: 100%;
    }
    .nopadtop {
        padding-top: 90px;
    }
    .service-box.m30 {
        margin: 15px 0;
    }
    .mobile-menu {
        display: block;
        background: #000;
        position: fixed;
        width: 100%;
        z-index: 9999;
        opacity: 0.92;
    }
    .vertical-header {
        display: none;
    }
    .transparent-header {
        left: 0;
        padding: 15px 0;
        position: relative;
        right: 0;
        top: 30px;
        z-index: 100;
        top: 0;
        background: #000;
    }
    #header.affix {
        position: relative;
    }
}

@media(min-width:768px) {
    .mobile-menu {
        display: none;
    }
    body.left-menu #wrapper {
        padding-left: 260px;
    }
    .vertical-header {
        display: block;
    }

}

@media(max-width:1198px) {
    .absolute-widget {
        margin-top: 0 !important;
    }
    .absolute-widget:before {
        display: none !important;
    }
    .footer .widget {
        margin: 30px 0;
    }
}

@media(max-width:998px) {
    .stat-count {
        margin: 15px 0;
    }
    .copyrights,
    .copyrights .text-right {
        text-align: center;
    }
    .home-message p {
        font-size: 48px;
    }
}

@media(max-width:550px) {
    .btn-wrapper .btn {
        display: block;
    }
    .callout .lead,
    .tagline-message h3 {
        font-size: 20px;
    }
    .home-message p {
        font-size: 28px;
    }
    .client-box {
        width: 100%;
        margin: 15px 0;
    }
    .client-box img {
        max-width: 240px;
    }
}

@media (min-width: 992px) {
    .dropmenu {
        position: relative;
    }
    .dropmenu:hover>ul {
        visibility: visible;
        opacity: 1;
        top: 100%;
    }
    .navbar-nav .dropmenu ul {
        -webkit-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
        transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
    }
    .dropmenu .dropdown-menu {
        min-width: 240px;
        padding: 0;
        border-radius: 0;
    }
    .dropmenu>ul {
        position: absolute;
        top: 130%;
        visibility: hidden;
        display: block;
        opacity: 0;
        left: 0;
    }
    .dropmenu>ul.start-right {
        right: 0;
        left: auto;
    }
    .dropmenu>ul li {
        position: relative;
    }
    .dropmenu>ul li:hover>ul {
        visibility: visible;
        opacity: 1;
        left: 100%;
    }
    .dropmenu>ul li:hover>ul.show-right {
        right: 100%;
    }
}
.videoTag {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    background-size: cover;
    overflow: hidden;
}
